<template>
  <div class="playListView">
    <van-sticky :offset-top="num">
      <div class="playListTop" ref="playListTop">
        <div class="left" @click="setAllPlay(), handleGetUrlOne()">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-bofang" />
          </svg>
          <p>{{playContent}}</p>
          <span v-if="playlist">({{ playlist.length }})</span>
        </div>
        <div class="right">
          <div>
            <span class="iconfont icon-icon_xinyong_xianxing_jijin-221"></span>
          </div>
          <span class="iconfont icon-xuanzhong"></span>
        </div>
      </div>
    </van-sticky>
    <play-list :playlist="playlist" />
  </div>
</template>
<script>
import { mapMutations, mapState } from "vuex"

import playList from '@/components/common/playList'

export default {
  name: "playListView",
  components: {
    playList
  },
  props: ["playlist", "num", "playContent"],
  computed: {
    ...mapState(["nowPlay", "playCurrentIndex", "showList"]),
  },
  beforeUnmount () {
    window.removeEventListener("scroll", this.handleScrollx, true)
  },
  mounted () {
    this.$nextTick(() => {
      window.addEventListener("scroll", this.handleScrollx, true)
    })
  },
  methods: {
    ...mapMutations(["setPlayIndex", "setMusicPlayFalse", "setAllPlay"]),
    handleScrollx () {
      if (this.$refs.playListTop.getBoundingClientRect().top < 62) {
        if (this.$parent.changeColor !== undefined) {
          this.$parent.changeColor(true)

        }
      } else {
        if (this.$parent.changeColor !== undefined) {
          this.$parent.changeColor(false)

        }
      }
    },
    handleGetUrlOne () {
      this.$store.dispatch("reqLyric", this.showList[0].id)
      this.$store.dispatch("reqSongUrl", this.showList[0].id)
    },
  },
}
</script>
<style lang="less" scoped>
.playListView {
  padding: 0 0.2rem 0.6rem 0.2rem;
  margin-top: 1.2rem;
  background-color: #fff;
}
.playListTop {
  position: relative;
  top: 0;
  left: 0;
  padding: 0.2rem 0;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  .left {
    display: flex;
    align-items: flex-end;
    svg {
      color: #ea443b;
      width: 0.4rem;
      height: 0.4rem;
    }
    p {
      font-size: 15px;
      font-weight: bold;
      letter-spacing: 1px;
      margin: 0 0.1rem 0 0.15rem;
    }
    span {
      font-size: 12px;
      color: #999;
    }
  }
  .right {
    flex: 1;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    span {
      font-size: 0.33rem;
      margin: 0 0.1rem 0 0.4rem;
    }
  }
}
</style>
