<template>
  <div class="playList">
    <div
      class="playListItem"
      v-for="(item, index) in playlist"
      :key="index"
      @click="setPlayIndex({ item, index });handleGetUrl(item.id)"
    >
      <p class="serialNumber" v-if="nowPlay.id !== item.id">{{ index + 1 }}</p>
      <span v-else class="iconfont icon-shuzhuangtu"></span>
      <div class="detail">
        <p :class="nowPlay.id == item.id ? 'name isActive' : 'name'">{{ item.name }}</p>
        <p class="author">
          <span>{{ item.ar[0].name }}</span>
          <span v-if="item.ar[1]">/{{ item.ar[1].name }}</span>
          <span v-if="item.ar[2]">/{{ item.ar[2].name }}</span>
          <span v-if="item.ar[3]">/{{ item.ar[3].name }}</span>
          - {{ item.al.name }}
        </p>
      </div>
      <div class="rightIcon">
        <span
          v-if="item.mv != 0"
          @click.stop="showMv(item)"
          class="iconfont icon-a-shipinti1beifen3"
        ></span>
        <span class="iconfont icon-gengduo"></span>
      </div>
    </div>
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex"

export default {
  name: "playList",
  props: ["playlist"],
  computed: {
    ...mapState(["nowPlay", "playCurrentIndex", "showList"]),
  },
  methods: {
    ...mapMutations(["setPlayIndex", "setMusicPlayFalse"]),

    // 点击获取播放歌曲url
    handleGetUrl (id) {
      this.$store.dispatch("reqLyric", id)
      this.$store.dispatch("reqSongUrl", id)
    },
    showMv (item) {
      console.log(item.mv);
    }
  },
}
</script>

<style lang="less" scoped>
.isActive {
  color: #dd3930 !important;
}
.playList {
  .playListItem:first-child {
    margin-top: 0.1rem;
  }
  .playListItem:last-child {
    margin: 0.4rem 0 !important;
  }
  .playListItem {
    display: flex;
    align-items: center;
    margin-top: 0.4rem;
    .icon-shuzhuangtu {
      color: #dd3930;
      padding-right: 0.2rem;
      display: flex;
      flex: 1;
      justify-content: center;
      font-size: 0.27rem;
    }
    .serialNumber {
      color: #999;
      padding-right: 0.2rem;
      display: flex;
      flex: 1;
      justify-content: center;
      font-size: 0.27rem;
    }
    .detail {
      flex: 15;
      .name {
        max-width: 260px;
        font-size: 0.28rem;
        color: #333;
        overflow: hidden;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
      }
      .author {
        max-width: 85%;
        font-size: 12px;
        color: #999;
        white-space: normal;
        overflow: hidden;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
      }
    }
    .rightIcon {
      color: #999;
      span {
        margin: 0 0.1rem 0 0.3rem;
      }
    }
  }
}
</style>