<template>
  <div class="singerView">
    <div v-if="singerDetailList.bg">
      <!-- 头部返回栏 -->
      <div :class="isColor ? 'singerViewTop color' : 'singerViewTop'">
        <span class="iconfont icon-zuo" @click="$router.go(-1)"></span>
        <span v-show="isColor">{{ singerDetailList.nm }}</span>
        <span class="iconfont icon-gengduo"></span>
      </div>
      <!-- 背景图片模块 -->
      <div class="bg">
        <img :src="singerDetailList.bg" alt />
        <div></div>
      </div>
      <!-- 歌手详情模块 -->
      <div class="singerDetail">
        <img v-if="singerDetailList.hd" class="avatarUrl" :src="singerDetailList.hd" alt />
        <div class="detail">
          <div class="detailName">
            <span
              :class="singerDetailList.icon ? 'author' : 'author author1'"
            >{{ singerDetailList.nm }}</span>
            <img v-if="singerDetailList.icon" class="titleUrl" :src="singerDetailList.icon" alt />
          </div>
          <div
            v-if="singerDetailList.icon"
            :class="
              singerDetailList.icon ? 'detailTow' : 'detailTow detailThree'
            "
          >
            <p>
              <span>{{ singerDetailList.fo }}</span>&nbsp;关注
            </p>
            <p>
              <span>{{ changePlayCount(singerDetailList.fd) }}</span>&nbsp;粉丝
            </p>
            <p>
              <span>Lv.{{ singerDetailList.lv }}</span>
            </p>
          </div>
          <div class="detailContent">
            <span>{{ singerDetailList.cn }}</span>
          </div>
          <div v-if="singerDetailList.icon">
            <div v-if="singerDetailList.is" class="detailFollow wd">已关注</div>
            <div v-else class="notFollow wd">
              <span class="iconfont icon-plus"></span> 关注
            </div>
          </div>
        </div>
      </div>
      <!-- 歌手单曲模块 -->
      <play-list-view :playlist="showList" :num="50" :playContent="'热门歌曲'" />
    </div>
  </div>
</template>
<script>
import { mapState, mapMutations } from "vuex"

import playListView from "@/components/ListView/playListView"
export default {
  name: "singer",
  components: {
    playListView
  },
  data () {
    return {
      // 标题是否变色
      isColor: false,
    }
  },
  computed: {
    ...mapState([
      "showList",
      "playList",
      "isPause",
      "isShow",
      "nowPlay",
      "singerDetailList",
    ]),
  },
  mounted () {
    this.$nextTick(() => {
      window.scrollTo(0, 0)
      document.body.scrollTop = 0
      document.documentElement.scrollTop = 0
    })
  },
  methods: {
    // 改变标题颜色
    changeColor (value) {
      this.isColor = value
    },
    // 拦截数字
    changePlayCount (num) {
      if (num >= 100000000) {
        return (num / 100000000).toFixed(0) + "亿"
      } else if (num >= 10000) {
        return (num / 10000).toFixed(1) + "万"
      } else {
        return num
      }
    },
    // 点击获取播放歌曲url
    handleGetUrl (id) {
      this.$store.dispatch("reqLyric", id)
      this.$store.dispatch("reqSongUrl", id)
    },
    handleGetUrlOne () {
      this.$store.dispatch("reqLyric", this.showList[0].id)
      this.$store.dispatch("reqSongUrl", this.showList[0].id)
    },
    ...mapMutations(["setPlayIndex", "setPlaylist", "setAllPlay"]),
  },
}
</script>
<style lang="less" scoped>
.isActive {
  color: #dd3930 !important;
}
.icon-shuzhuangtu {
  color: #dd3930;
  margin-right: 0.27rem;
}
.color {
  color: #000 !important;
  background-color: #fff;
}
.singerView {
  width: 7.5rem;
  min-height: 100vh;
  background-image: linear-gradient(#efefef, #fff);
  .singerViewTop {
    position: fixed;
    top: 0;
    left: 0;
    width: 7.5rem;
    height: 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 0.3rem;
    transition: all 0.3s;
    z-index: 3;
    color: #fff;

    .iconfont {
      font-size: 0.4rem;
    }
    .icon-fenxiang {
      font-size: 0.45rem;
    }
  }
  .bg {
    width: 7.5rem;
    height: 6.1rem;
    overflow: hidden;
    div {
      position: absolute;
      top: 0;
      left: 0;
      width: 7.5rem;
      height: 4rem;
      background: -webkit-linear-gradient(top, #4e4e4e3d 0%, #6b020200 100%);
    }
    img {
      width: 7.5rem;
      transform: scale(1.1);
    }
  }
  .singerDetail {
    position: relative;
    top: -0.64rem;
    background-color: #fff;
    padding: 0.3rem;
    margin: 0.3rem 0.3rem 0 0.3rem;
    display: flex;
    align-items: center;
    flex-direction: column;
    border-radius: 20px;
    .avatarUrl {
      position: absolute;
      top: -0.9rem;
      width: 1.3rem;
      height: 1.3rem;
      border-radius: 50%;
      border: 0.15em solid #fff;
      box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2);
    }
    .detail {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin-top: 0.3rem;
      .detailName {
        display: flex;
        align-items: center;
        .author1 {
          margin-left: 0 !important;
        }
        .author {
          font-size: 0.36rem;
          font-weight: bold;
          margin-left: 0.4rem;
        }
        .titleUrl {
          width: 0.28rem;
          height: 0.28rem;
          margin-left: 0.1rem;
        }
      }
      .detailThree {
        justify-content: center !important;
      }
      .detailTow {
        display: flex;
        justify-content: space-between;
        width: 3rem;
        line-height: 2;
        p {
          font-size: 0.2rem;
          color: #999;
          span {
            font-size: 0.23rem;
            color: #222;
          }
        }
      }
      .detailContent {
        span {
          font-size: 0.23rem;
          color: #999;
          letter-spacing: 1px;
        }
      }
      .wd {
        margin-top: 0.2rem;
        padding: 8px 40px;
        border-radius: 20px;
        font-size: 0.25rem;
      }
      .detailFollow {
        color: #909090;
        background-color: #f7f7f9;
      }
      .notFollow {
        color: rgb(240, 240, 240);
        background-color: #fe241a;
        .icon-plus {
          display: inline-block;
          transform: scale(0.7);
        }
      }
    }
  }
  .playListView {
    margin-top: 0;
    background-color: #fff;
  }
  .playListTop {
    height: 0.8rem;
    // position: fixed;
    // top: 1rem;
    // left: 0;
    background-color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .left {
      display: flex;
      align-items: flex-end;
      svg {
        color: #ea443b;
        width: 0.4rem;
        height: 0.4rem;
      }
      p {
        font-size: 15px;
        font-weight: bold;
        margin: 0 0.1rem 0 0.15rem;
      }
      span {
        font-size: 12px;
        color: #999;
      }
    }
    .right {
      flex: 1;
      display: flex;
      align-items: flex-end;
      justify-content: flex-end;
      span {
        font-size: 0.33rem;
        margin: 0 0.1rem 0 0.4rem;
      }
    }
  }
  .playList {
    .playListItem:last-child {
      margin: 0.4rem 0 !important;
    }
    .playListItem:first-child {
      margin-top: 0.1rem;
    }
    .playListItem {
      display: flex;
      align-items: center;
      margin-top: 0.4rem;
      .serialNumber {
        color: #999;
        padding-right: 0.2rem;
        display: flex;
        flex: 1;
        justify-content: center;
      }
      .detail {
        flex: 15;
        .name {
          max-width: 260px;
          font-size: 0.28rem;
          color: #333;
          letter-spacing: 2px;
          overflow: hidden;
          display: -webkit-box;
          -webkit-line-clamp: 1;
          -webkit-box-orient: vertical;
        }
        .author {
          font-size: 12px;
          color: #999;
          white-space: normal;
          overflow: hidden;
          display: -webkit-box;
          -webkit-line-clamp: 1;
          -webkit-box-orient: vertical;
        }
      }
      .rightIcon {
        color: #999;
        span {
          margin: 0 0.1rem 0 0.3rem;
        }
      }
    }
  }
}
</style>
